.homeTitle {
  font-weight: 600;
  text-transform: uppercase;
  font-size: 60px;
  text-align: center;
}
.homeContainer {
  padding: 0;
  margin: 0 auto;
  width: 97%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.homeItem {
  list-style: none;
  margin-bottom: 15px;
}
.movieTitleCont {
  width: 300px;

  overflow: hidden;
  text-align: center;
}
.movieTitle {
  margin: 0;
  margin-top: 15px;
}
.homeLink {
  text-decoration: none;
}
@media (max-width: 600px) {
  .homeContainer {
    flex-direction: column;
    align-items: center;
  }
} ;
